const { Component } = React

const BigNumber = ({ children }) => 
  <div className="big-number">
    {children}
    <style jsx>{`
      .big-number {
        font-family: Helvetica;
        font-size: 20rem;
        color: white;
      }
    `}</style>
  </div>

class Page extends Component {
  
  state = { scrollPosition: 0 } 

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll)
  }

  handleScroll = e => {
    this.setState({ scrollPosition: e.pageY })
    console.log(this.state)
  }
 
  render(){
    return ( 
      <div className="root">
        <BigNumber>0</BigNumber>
        <style jsx>{`
          .root {
            background: #000;
            height: 100vh;
            width: 100vw;
            display: flex;
            justify-content: center;
            align-items: center;
          }
        `}</style>
      </div>
    )
  }
}

export default Page



